import React from 'react';

import { screen, fonts, colors } from '../constants';

import Container from './Container';

export default function BlockTimeline({
  pretitle,
  title,
  desc,
  items,
  titleClass,
}) {
  return (
    <div className="block-timeline">
      <Container>
        <div className="block-timeline__wrap">
          {pretitle && (
            <div className="block-timeline__pretitle app__pretitle">
              {pretitle}
            </div>
          )}
          {title && (
            <h1 className={`block-timeline__title app__title ${titleClass}`}>
              {title}
            </h1>
          )}
          {desc && <div className="block-timeline__desc app__desc">{desc}</div>}

          <div className="block-timeline__items app__row">
            {items.map((item, i) => (
              <div key={`${item.title}-${i}`} className="block-timeline__item">
                <div className="block-timeline__item-container">
                  <div className="block-timeline__item-wrap">
                    {item.pretitle && (
                      <div className="block-timeline__item-pretitle">
                        {item.pretitle}
                      </div>
                    )}
                    <h3 className="block-timeline__item-title">{item.title}</h3>
                    <div className="block-timeline__item-content">
                      {item.content}
                    </div>
                    <div className="block-timeline__item-sub-content">
                      {item.subContent}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>

          <style jsx>{`
            .block-timeline__wrap {
              justify-content: center;
            }

            .block-timeline__title {
              margin-right: auto;
              margin-left: auto;
              font-weight: normal;
              font-size: 40px;
              line-height: 48px;
            }

            .block-timeline__desc {
              max-width: 800px;
              margin-right: auto;
              margin-left: auto;
              margin-bottom: 50px;
            }

            .block-timeline__item-wrap::before {
              content: '';
              width: 20px;
              height: 21px;
              position: absolute;
              background-color: ${colors.darkSlateBlue};
              border-radius: 50%;
            }
            @media (min-width: ${screen.tabletMax + 1}px) {
              .block-timeline__item {
                text-align: left;
                width: 25%;
                position: relative;
                border-top: 1px solid #223664;
              }
              .block-timeline__item * {
                box-sizing: border-box;
              }
              .block-timeline__item-container {
                position: relative;
              }
              .block-timeline__item-wrap {
                position: absolute;
                width: 100%;
                z-index: 2;
              }
              .block-timeline__item:nth-child(2n + 1) {
                padding-right: 26px;
              }
              .block-timeline__item:nth-child(2n + 1)
                .block-timeline__item-wrap {
                bottom: 0;
                border-right: 1px solid #223664;
                padding: 0 30px 47px 0;
              }
              .block-timeline__item:nth-child(2n + 1)
                .block-timeline__item-wrap::before {
                bottom: -10px;
                right: -10px;
              }
              .block-timeline__item:nth-child(2n) {
                padding-left: 26px;
              }
              .block-timeline__item:nth-child(2n) .block-timeline__item-wrap {
                top: 0;
                border-left: 1px solid #223664;
                padding: 29px 0 10px 30px;
              }
              .block-timeline__item:nth-child(2n)
                .block-timeline__item-wrap::before {
                top: -10px;
                left: -10px;
              }

              .block-timeline__items {
                margin-top: 148px;
                justify-content: center;
                padding: 216px 0;
                position: relative;
              }
              .block-timeline__items::before,
              .block-timeline__items::after {
                content: '';
                position: absolute;
                width: 102px;
                height: 139px;
                top: 50%;
                margin-top: -70px;
                z-index: 1;
              }
              .block-timeline__items::before {
                background: linear-gradient(
                  180deg,
                  #ffffff 25.97%,
                  rgba(255, 255, 255, 0) 100%
                );
                transform: matrix(0, 1, 1, 0, 0, 0);
                left: 0;
              }
              .block-timeline__items::after {
                background: linear-gradient(
                  180deg,
                  #ffffff 25.97%,
                  rgba(255, 255, 255, 0) 100%
                );
                transform: rotate(90deg);
                right: 0;
              }
            }

            .block-timeline__item-title {
              font-family: ${fonts.halyardDisplay};
              font-size: 30px;
              font-weight: 400;
              line-height: 45px;
              margin: 0;
            }
            .block-timeline__item-content {
              font-size: 22px;
              line-height: 30px;
              color: ${colors.darkSlateBlue};
              opacity: 0.6;
              width: 110%;
            }
            .block-timeline__item-sub-content {
              font-size: 14px;
              line-height: 17px;
              color: ${colors.darkSlateBlue};
            }

            @media (max-width: ${screen.tabletMax}px) {
              .block-timeline__item .block-timeline__item-wrap {
                position: relative;
                padding: 0 0 40px 30px;
                border-left: 1px solid #223664;
              }
              .block-timeline__item .block-timeline__item-wrap::before {
                top: 13px;
                left: -10px;
              }
              .block-timeline__item:first-child .block-timeline__item-wrap {
                padding-top: 20px;
              }
              .block-timeline__item:first-child
                .block-timeline__item-wrap::before {
                top: 32px;
              }
              .block-timeline__item:last-child {
                margin-bottom: 0;
              }

              .block-timeline__items {
                position: relative;
              }
              .block-timeline__items::before,
              .block-timeline__items::after {
                content: '';
                position: absolute;
                width: 28px;
                height: 45px;
                left: -14px;
                z-index: 1;
              }
              .block-timeline__items::before {
                background: linear-gradient(
                  180deg,
                  #ffffff 25.97%,
                  rgba(255, 255, 255, 0) 100%
                );
                top: -10px;
              }
              .block-timeline__items::after {
                background: linear-gradient(
                  180deg,
                  #ffffff 25.97%,
                  rgba(255, 255, 255, 0) 100%
                );
                transform: rotate(180deg);
                bottom: 0;
              }

              .block-timeline__item-content {
                width: 100%;
              }
            }
          `}</style>
          <style jsx global>{`
            .block-timeline__item-pretitle {
              font-family: ${fonts.halyardDisplay};
              font-size: 40px;
              font-weight: 400;
              line-height: 32px;
              letter-spacing: 0px;
              color: ${colors.darkSlateBlue};
              margin-bottom: 5px;
            }

            .block-timeline__item-pretitle span {
              font-size: 24px;
              font-weight: 400;
              display: inline-block;
              line-height: 28px;
              padding: 8px 30px;
              background-color: ${colors.darkSlateBlue};
              border-radius: 10px;
              color: #fff;
            }
            .block-timeline__item-pretitle span.empty {
              padding: 0;
            }
          `}</style>
        </div>
      </Container>
    </div>
  );
}
