import React, { useLayoutEffect, useState } from 'react';
import { navigate } from 'gatsby';

import { screen, fonts, colors } from '../constants';
import { trackSegment } from '../utils/segment';
import { getCampaign } from '../utils/utm';

import Container from './Container';
import Button from './Button';
import CheckIcon from './icons/Check';

export default function BlockPricingPlans({
  pretitle,
  title,
  desc,
  items,
  titleClass,
  title_align = 'left',
  notes,
}) {
  const [planContentHeight, setPlanContentHeight] = useState('auto');

  useLayoutEffect(() => {
    function updateSize() {
      const planContentContainers = document.querySelectorAll(
        '.block-pricing-plans__item-content-wrap'
      );
      let maxPlanHeight = 0;
      planContentContainers.forEach(item => {
        if (item.clientHeight > maxPlanHeight) {
          maxPlanHeight = item.clientHeight;
        }
      });
      setPlanContentHeight(`${maxPlanHeight}px`);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);

  function handleButtonTo(to, planName) {
    trackSegment('Pricing page CTA clicked', {
      planName,
      context: {
        campaign: getCampaign(),
      },
    });
    navigate(to);
  }

  return (
    <div className="block-pricing-plans">
      <Container>
        <div className="block-pricing-plans__wrap">
          {pretitle && (
            <div className="block-pricing-plans__pretitle app__pretitle">
              {pretitle}
            </div>
          )}
          {title && (
            <h1
              className={`block-pricing-plans__title app__title ${titleClass}`}
            >
              {title}
            </h1>
          )}
          {desc && (
            <div className="block-pricing-plans__desc app__desc">{desc}</div>
          )}

          <div className="block-pricing-plans__items app__row">
            {items.map((item, i) => (
              <div
                key={`${item.title}-${i}`}
                className="block-pricing-plans__item"
              >
                <div className="block-pricing-plans__item-general">
                  <h3 className="block-pricing-plans__item-title">
                    {item.title}
                  </h3>
                  <div
                    className="block-pricing-plans__item-content"
                    style={{ height: planContentHeight }}
                  >
                    <div className="block-pricing-plans__item-content-wrap">
                      {item.content}
                    </div>
                  </div>
                  <div className="block-pricing-plans__item-action">
                    {item.ctaText && (
                      <Button
                        onClick={() =>
                          handleButtonTo(item.ctaTo, item.planName)
                        }
                        primary
                        large
                        hasArrow
                      >
                        {item.ctaText}
                      </Button>
                    )}
                  </div>
                </div>
                <div className="block-pricing-plans__item-specific">
                  <h4 className="block-pricing-plans__item-specific-title">
                    Subscription price
                  </h4>
                  <div className="block-pricing-plans__item-specific-content">
                    {item.subscriptionPricePrefix ? (
                      <span className="pretext">
                        {item.subscriptionPricePrefix}
                      </span>
                    ) : (
                      ''
                    )}
                    <span className="unit">$</span>
                    <span className="number">{item.subscriptionPrice}</span>
                    <span className="per">/mo</span>
                  </div>
                </div>
                <div className="block-pricing-plans__item-specific">
                  <h4 className="block-pricing-plans__item-specific-title">
                    Max credit line per customer
                  </h4>
                  <div className="block-pricing-plans__item-specific-content">
                    <span className="unit-text">$</span>
                    <span className="number-text">
                      {item.maxCreditLinePerCustomer}
                    </span>
                  </div>
                </div>
                <div className="block-pricing-plans__item-specific">
                  <ul className="block-pricing-plans__item-specific-feature-list">
                    {item.featuresBasic.map((feature, i) => (
                      <li key={`${feature}-${i}`}>
                        <span className="icon">
                          <CheckIcon />
                        </span>
                        <span className="text">{feature}</span>
                      </li>
                    ))}
                  </ul>
                  <ul className="block-pricing-plans__item-specific-feature-list">
                    {item.featuresExtend.map((feature, i) => (
                      <li key={`${feature}-${i}`}>
                        {feature !== '' && (
                          <span className="icon">
                            <CheckIcon />
                          </span>
                        )}
                        <span className="text">
                          {feature !== '' ? (
                            feature
                          ) : (
                            <br className="fake-br" />
                          )}
                        </span>
                      </li>
                    ))}
                  </ul>
                  <h4 className="block-pricing-plans__item-specific-title">
                    Resolve fee
                    <br />
                    (On invoice total - 90% Advance Rate)
                  </h4>
                  <ul className="block-pricing-plans__item-specific-fee-list">
                    {item.resolveFee.map((fee, i) => (
                      <li key={`${fee.type}-${i}`}>
                        <span className="type">
                          {fee.type}
                          {fee.value ? ':' : ''}
                        </span>
                        <span className={`value ${fee.bold ? 'bold' : ''}`}>
                          {fee.value}
                        </span>
                      </li>
                    ))}
                  </ul>
                </div>
              </div>
            ))}
          </div>
          <ul className="block-pricing-plans__notes-list">
            {notes.map((note, i) => (
              <li key={i}>{note}</li>
            ))}
          </ul>
          <style jsx>{`
            .block-pricing-plans__wrap {
              justify-content: center;
            }

            .block-pricing-plans__title {
              margin-right: auto;
              margin-left: auto;
              text-align: ${title_align};
              font-weight: 400;
              font-size: 56px;
              line-height: 72px;
              letter-spacing: 0.5px;
              margin: 0 auto 50px auto;
              max-width: 910px;
            }

            .block-pricing-plans__desc {
              max-width: 800px;
              margin-right: auto;
              margin-left: auto;
              margin-bottom: 50px;
            }

            .block-pricing-plans__item:last-child {
              margin-right: 0;
            }

            .block-pricing-plans__items {
              margin-top: 92px;
            }

            .block-pricing-plans__item {
              text-align: left;
              width: 33.33%;
              margin-right: 16px;
              border: 1px solid #acb8cc;
              border-radius: 20px;
              overflow: hidden;
            }

            .block-pricing-plans__item-general {
              padding: 18px 25px 27px;
              position: relative;
            }
            .block-pricing-plans__item-general::before {
              content: '';
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              background: linear-gradient(
                315deg,
                rgba(254, 93, 100, 0.388615) 8.86%,
                rgba(254, 93, 100, 0.805169) 108.86%
              );
              opacity: 0.43;
              z-index: -1;
            }

            .block-pricing-plans__item-title {
              font-family: ${fonts.halyardDisplay};
              font-weight: 400;
              margin: 0 0 10px;
              line-height: 40px;
              font-size: 36px;
              letter-spacing: 0.0533333px;
              color: ${colors.indigo};
            }
            .block-pricing-plans__item-content {
              font-size: 20px;
              line-height: 28px;
              letter-spacing: 0.5px;
              color: ${colors.darkSlateBlue};
            }
            .block-pricing-plans__item-action {
              margin-top: 21px;
            }

            .block-pricing-plans__item-specific {
              padding: 20px 25px;
              font-weight: normal;
            }
            .block-pricing-plans__item-specific
              + .block-pricing-plans__item-specific {
              border-top: 1.31781px dashed #acb8cc;
            }
            .block-pricing-plans__item-specific-title {
              margin: 0;
              font-size: 16px;
              font-weight: 500;
              line-height: 20px;
              text-transform: uppercase;
              color: ${colors.coolGrey};
            }
            .block-pricing-plans__item-specific-content {
              white-space: nowrap;
              font-weight: normal;
            }
            .block-pricing-plans__item-specific-content .pretext {
              font-size: 34px;
              vertical-align: bottom;
              margin-right: 5px;
            }
            .block-pricing-plans__item-specific-content .number {
              font-size: 42.1699px;
              line-height: 125%;
              font-feature-settings: 'tnum' on, 'lnum' on;
              color: ${colors.darkSlateBlue};
            }
            .block-pricing-plans__item-specific-content .unit {
              vertical-align: top;
              font-size: 24px;
              line-height: 40px;
            }
            .block-pricing-plans__item-specific-content .per {
              vertical-align: bottom;
              font-size: 24px;
              line-height: 40px;
            }
            .block-pricing-plans__item-specific-content .number-text {
              font-size: 34.2631px;
              line-height: 47px;
              font-feature-settings: 'tnum' on, 'lnum' on;
              color: ${colors.darkSlateBlue};
              vertical-align: middle;
            }
            .block-pricing-plans__item-specific-content .unit-text {
              vertical-align: middle;
              font-size: 24.5851px;
              line-height: 34px;
            }
            .block-pricing-plans__item-specific-feature-list {
              padding-left: 0;
              list-style: none;
              margin: 0 0 50px;
            }
            .block-pricing-plans__item-specific-feature-list li {
              font-size: 22px;
              line-height: 31px;
            }
            .block-pricing-plans__item-specific-feature-list li .icon,
            .block-pricing-plans__item-specific-feature-list li .text {
              vertical-align: middle;
            }
            .block-pricing-plans__item-specific-feature-list li .icon {
              padding-top: 3px;
              margin-right: 8px;
            }

            .block-pricing-plans__item-specific-fee-list {
              padding-left: 0;
              list-style: none;
            }
            .block-pricing-plans__item-specific-fee-list li {
              font-size: 24px;
              line-height: 30px;
              font-feature-settings: 'tnum' on, 'lnum' on;
              color: ${colors.coolGrey};
            }
            .block-pricing-plans__item-specific-fee-list li .type {
              margin-right: 5px;
            }
            .block-pricing-plans__item-specific-fee-list li .value.bold {
              color: ${colors.darkSlateBlue};
            }

            .block-pricing-plans__notes-list {
              padding-left: 0;
              list-style: none;
              font-size: 20px;
              line-height: 150%;
              font-weight: normal;
              letter-spacing: 0.0045em;
              color: ${colors.coolGrey};
              margin-top: 33px;
            }
            .block-pricing-plans__notes-list li::before {
              content: '';
            }

            .block-pricing-plans__item-content-wrap {
              letter-spacing: 0.054px;
              color: ${colors.indigo};
            }

            @media (max-width: ${screen.tabletMax}px) {
              .block-pricing-plans__item {
                margin: 0 0 16px 0;
                width: 100%;
              }

              .block-pricing-plans__item:last-child {
                margin-bottom: 0;
              }

              .block-pricing-plans__item-content {
                height: auto !important;
              }
            }
            @media (max-width: ${screen.mobileMax}px) {
              .block-pricing-plans__title {
                font-size: 40px;
                line-height: 48px;
                letter-spacing: 0.5px;
                margin: 0 0 47px 0;
              }

              .block-pricing-plans__item-title {
                margin: 0 0 8px;
                line-height: 33px;
                font-size: 30px;
                letter-spacing: 0.0438689px;
              }
              .block-pricing-plans__item-content {
                font-size: 16px;
                line-height: 23px;
                letter-spacing: 0.0438689px;
              }
              .block-pricing-plans__item-action {
                margin-top: 8px;
              }

              .block-pricing-plans__item-specific-title {
                font-weight: 500;
                font-size: 12px;
                line-height: 125%;
              }
              .block-pricing-plans__item-specific-content .pretext {
                font-size: 34px;
                vertical-align: bottom;
                margin-right: 5px;
              }
              .block-pricing-plans__item-specific-content .number {
                font-size: 30px;
              }
              .block-pricing-plans__item-specific-content .unit {
                font-size: 20.2223px;
                line-height: 28px;
              }
              .block-pricing-plans__item-specific-content .per {
                font-size: 20.2223px;
                line-height: 28px;
              }
              .block-pricing-plans__item-specific-content .number-text {
                font-size: 30px;
                line-height: 41px;
              }
              .block-pricing-plans__item-specific-content .unit-text {
                font-size: 20.2223px;
                line-height: 28px;
              }
              .block-pricing-plans__item-specific-feature-list {
                margin-bottom: 20px;
              }
              .block-pricing-plans__item-specific-feature-list li {
                font-size: 16px;
                line-height: 22px;
              }
              .block-pricing-plans__item-specific-fee-list li {
                font-size: 16px;
                line-height: 20px;
              }

              .block-pricing-plans__notes-list {
                font-size: 16px;
                line-height: 24px;
                letter-spacing: 0.0045em;
              }
            }
          `}</style>
          <style jsx global>{`
            .block-pricing-plans__item-pretitle {
              font-family: ${fonts.halyardDisplay};
              font-size: 40px;
              font-weight: 400;
              line-height: 48px;
              letter-spacing: 0px;
              color: ${colors.darkSlateBlue};
            }

            .block-pricing-plans__item-pretitle span {
              font-size: 38.5px;
              font-weight: 500;
            }
          `}</style>
        </div>
      </Container>
    </div>
  );
}
